import { ChartSeriesData, ValueFormats, Group } from '../UNSAFE_visTypes/chart';
import { VisTabularDatatip } from '../../PRIVATE_VisTabularDatatip';
import { TableRowData } from '../../PRIVATE_VisTabularDatatip';
import { LineAreaItem } from '#UNSAFE_LineAreaChart/lineAreaChart.types';
import { ScatterItem } from '#UNSAFE_ScatterChart/scatterChart.types';
import { BarItemData } from '#UNSAFE_BarChart/barChart.types';

/**
 * Returns the data required for the VisTabularDatatip to render.
 * @param activeInfo active item info.
 * @param series The array of chart series.
 * @param groups The array of chart groups.
 * @param valueFormats The array of chart series.
 * @param getDataItem function which returns chartItem
 */
export function getChartTabularData<K, D extends LineAreaItem<K> | ScatterItem<K> | BarItemData<K>>(
  data: D,
  seriesValue: string,
  groupValue: string,
  valueFormats?: ValueFormats,
  isTimeAxis?: boolean,
  formatAxisLabel?: (axisValue: number) => string,
  formatYValue?: (value: number) => string,
  hasXYValues?: boolean
) {
  //TODO: Use translation strings
  let itemValue, itemX, itemY;
  if (hasXYValues && 'y' in data) {
    const x = data.x;
    itemX = valueFormats?.x?.format ? valueFormats?.value?.format?.(x!) : formatYValue?.(x!)!;

    const y = data.y;
    itemY = valueFormats?.y?.format ? valueFormats?.value?.format?.(y!) : formatYValue?.(y!)!;
  } else if ('value' in data && data.value !== undefined) {
    const value = data.value;
    itemValue = valueFormats?.value?.format
      ? valueFormats?.value?.format?.(value)
      : formatYValue?.(value)!;
  }
  const tableData: TableRowData[] = [];

  if (valueFormats?.series?.isDisplayed != false) {
    tableData.push({
      key: valueFormats?.series?.label || 'Series',
      value: seriesValue
    });
  }
  if (valueFormats?.group?.isDisplayed != false) {
    tableData.push({
      key: valueFormats?.group?.label || (isTimeAxis ? 'Date' : 'Group'),
      value: isTimeAxis ? formatAxisLabel?.(new Date(groupValue).getTime())! : groupValue
    });
  }
  if (valueFormats?.value?.isDisplayed != false) {
    if (hasXYValues) {
      tableData.push({
        key: valueFormats?.value?.label || 'X',
        value: itemX!
      });
      tableData.push({
        key: valueFormats?.value?.label || 'Y',
        value: itemY!
      });
    } else {
      tableData.push({
        key: valueFormats?.value?.label || 'Value',
        value: itemValue!
      });
    }
  }
  return tableData;
}

export function getDefaultDatatipRenderer<
  K extends string | number,
  D extends LineAreaItem<K> | ScatterItem<K> | BarItemData<K>
>(options: {
  series: ChartSeriesData<K, D>;
  groups: Group;
  valueFormats?: ValueFormats;
  timeAxisType?: 'mixedFrequency' | 'enabled' | 'skipGaps';
  formatAxisLabel?: (axisValue: number) => string;
  formatYValue?: (value: number) => string;
  hasXYValues?: boolean;
}) {
  const isMixedFrequency = options.timeAxisType === 'mixedFrequency';
  const isRegularTimeAxis =
    options.timeAxisType === 'enabled' || options.timeAxisType === 'skipGaps';

  return (context: { data: D }) => {
    const groupValue = options.groups.name || options.groups.id;
    const timeValue = (
      isMixedFrequency ? context.data.x : isRegularTimeAxis ? groupValue : undefined
    ) as string;
    return {
      content: (
        <VisTabularDatatip
          tableData={getChartTabularData<K, D>(
            context.data,
            options.series.name || options.series.id.toString(),
            timeValue || options.groups.name || options.groups.id,
            options.valueFormats,
            options.timeAxisType != null,
            options.formatAxisLabel,
            options.formatYValue,
            options.hasXYValues
          )}
        />
      ),
      borderColor: context.data.color || options.series.color
    };
  };
}
